import TripEditPointView from '../view/trip-edit-point-view';
import TripPointView from '../view/trip-point-view';
import { replace, render, remove } from '../framework/render';
import { Mode } from '../const';
import { UserAction, UpdateType } from '../const';
import { isDateChange } from '../utils/point';

export default class TripPointPresenter {

  #tripList = null;
  #tripPointData = null;
  #point = null;
  #editPoint = null;
  #handleDataChange = null;
  #mode = Mode.DEFAULT;
  #handleModeChange = null;
  #offersData = null;
  #destinationsData = null;


  constructor({tripList, onDataChange, onModeChange}) {
    this.#tripList = tripList;
    this.#handleDataChange = onDataChange;
    this.#handleModeChange = onModeChange;

  }

  init(tripPointData, offers, destinations) {

    this.#tripPointData = tripPointData;

    /*   .          */
    this.#offersData = offers;

    this.#destinationsData = destinations;

    /* ,     #point  #editPoint  ,   
        */
    const prevPoint = this.#point;
    const prevEditPoint = this.#editPoint;

    /*   view  ,            ,
            " " */
    this.#point = new TripPointView({
      tripPoint: this.#tripPointData,
      offers: this.#offersData.offers,
      destinations: this.#destinationsData.destinations,
      /*    " "  view  ,   view   */
      onEditClick: this.#handleEditClick,
      onFavoriteClick: this.#handleFavoriteClick,
    });


    this.#editPoint = new TripEditPointView({
      editTripPoint: this.#tripPointData,
      offers: this.#offersData.offers,
      destinations: this.#destinationsData.destinations,
      /*    " "  view  ,   view   */
      onSubmitClick: this.#handleSubmitClick,
      onArrowClick: this.#handleArrowClick,
      onDeleteClick: this.#handleDeleteClick
    });

    /*  (  init)   ,       ,
      -            return */
    if(prevPoint === null || prevEditPoint === null){
      render(this.#point, this.#tripList.element);
      return;
    }

    /*      init()       ,     init()
         ,  - replace,   ()  ,     init() */
    if(this.#mode === Mode.DEFAULT){
      replace(this.#point, prevPoint);
    }

    if(this.#mode === Mode.EDITING){
      replace(this.#editPoint, prevEditPoint);
    }

    remove(prevPoint);
    remove(prevEditPoint);
  }

  destroy() {
    remove(this.#point);
    remove(this.#editPoint);
  }

  #handleEditClick = () => {
    this.#replacePointToEditPoint();
  };

  #handleArrowClick = () => {
    this.#editPoint.reset(this.#tripPointData);
    this.#replaceEditPointToPoint();
  };

  #handleSubmitClick = (updateTripPointData) => {
    const isMinorUpdateType = !isDateChange(this.#tripPointData, updateTripPointData);


    this.#handleDataChange(
      UserAction.UPDATE_POINT,
      isMinorUpdateType ? UpdateType.MINOR : UpdateType.PATCH,
      updateTripPointData
    );
    this.#replaceEditPointToPoint();
  };

  /* ,        ,  ,
      ,          isFavorite */
  #handleFavoriteClick = () => {
    /*  (...)  ,  tripPointData      
     */
    this.#handleDataChange(
      UserAction.UPDATE_POINT,
      UpdateType.MINOR,
      {...this.#tripPointData, isFavorite: !this.#tripPointData.isFavorite}
    );
  };

  #handleDeleteClick = (tripPointData) => {
    this.#handleDataChange(
      UserAction.DELETE_POINT,
      UpdateType.MINOR,
      tripPointData
    );
  };

  #escKeyDownHandler = (event) => {
    if(event.key === 'Escape'){
      event.preventDefault();
      this.#editPoint.reset(this.#tripPointData);
      this.#replaceEditPointToPoint();
    }
  };

  #replacePointToEditPoint() {
    replace(this.#editPoint, this.#point);
    document.addEventListener('keydown', this.#escKeyDownHandler);
    /*        ,      mode: editting  resetView()
     */
    this.#handleModeChange();
    this.#mode = Mode.EDITING;
  }

  #replaceEditPointToPoint() {
    replace(this.#point, this.#editPoint);
    document.removeEventListener('keydown', this.#escKeyDownHandler);
    this.#mode = Mode.DEFAULT;
  }

  /* ,    ,      ,        */
  resetView() {
    if (this.#mode !== Mode.DEFAULT) {
      this.#editPoint.reset(this.#tripPointData);
      this.#replaceEditPointToPoint();
    }
  }

}
